<template>
    <div class="row justify-content-center">
        <div class="col-md-auto offset-md-auto col-11" v-if="showEquipment">
            <table class="table table-light table-hover rounded">
                <thead>
                    <template v-if="isMobile">
                        <tr>
                            <th scope="col" style="overflow:visible; width:30%;">{{ text.weapon }}</th>
                            <th scope="col" style="width:25%;"></th>
                            <th scope="col"></th>
                        </tr>
                        <tr>
                            <th scope="col" colspan="3">
                                <div class="input-group w-50">
                                    <span class="input-group-text">{{ text.level }}:</span>
                                    <input type="number" aria-label="level" class="form-control" 
                                    style="background: transparent; font-weight: bold;" 
                                    min="1" v-model="level" @input="setLevel">
                                </div>
                            </th>
                        </tr>
                        <tr>
                            <th scope="col" colspan="3">
                                <div class="input-group" role="group" @click="setStar">
                                    <span class="input-group-text">STARS:</span>
                                    <button type="button" class="btn btn-outline-dark btn-sm" :class="{ active: star === 0 }" @click="star = 0">0</button>
                                    <button type="button" class="btn btn-outline-dark btn-sm" :class="{ active: star === 1 }" @click="star = 1">1</button>
                                    <button type="button" class="btn btn-outline-dark btn-sm" :class="{ active: star === 2 }" @click="star = 2">2</button>
                                    <button type="button" class="btn btn-outline-dark btn-sm" :class="{ active: star === 3 }" @click="star = 3">3</button>
                                    <button type="button" class="btn btn-outline-dark btn-sm" :class="{ active: star === 4 }" @click="star = 4">4</button>
                                    <button type="button" class="btn btn-outline-dark btn-sm" :class="{ active: star === 5 }" @click="star = 5">5</button>
                                </div>
                            </th>
                        </tr>
                    </template>
                    <tr v-else>
                        <th scope="col" style="width: 15%;">{{ text.weapon }}</th>
                        <th scope="col" style="width: 50%;">
                            <div class="input-group w-50">
                                <span class="input-group-text">{{ text.level }}:</span>
                                <input type="number" aria-label="level" class="form-control" 
                                style="background: transparent; font-weight: bold;" 
                                min="1" v-model="level" @input="setLevel">
                            </div>
                        </th>
                        <th scope="col">
                            <div class="input-group d-flex justify-content-end" role="group" @click="setStar">
                                <span class="input-group-text">STARS:</span>
                                <button type="button" class="btn btn-outline-dark btn-sm" :class="{ active: star === 0 }" @click="star = 0">0</button>
                                <button type="button" class="btn btn-outline-dark btn-sm" :class="{ active: star === 1 }" @click="star = 1">1</button>
                                <button type="button" class="btn btn-outline-dark btn-sm" :class="{ active: star === 2 }" @click="star = 2">2</button>
                                <button type="button" class="btn btn-outline-dark btn-sm" :class="{ active: star === 3 }" @click="star = 3">3</button>
                                <button type="button" class="btn btn-outline-dark btn-sm" :class="{ active: star === 4 }" @click="star = 4">4</button>
                                <button type="button" class="btn btn-outline-dark btn-sm" :class="{ active: star === 5 }" @click="star = 5">5</button>
                            </div>
                        </th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="item in T5_Weapon" :key="item">
                        <th scope="row" style="width:20%;">
                            <img :src="`img/EquipmentItem/${item.imgName}.png`" 
                            :alt="item.imgName">
                        </th>
                        <td class="align-middle">{{ text.atk }}:{{item.finalATK}}</td>
                        <td class="align-middle">
                            <p v-if="item.stat1">+{{ item.stat1.text }}&nbsp;{{item.stat1.value}}<span v-if="item.stat1.percent">%</span></p>
                            <p v-if="item.stat2">+{{ item.stat2.text }}&nbsp;{{item.stat2.value}}<span v-if="item.stat2.percent">%</span></p>
                            <p v-if="item.stat3">+{{ item.stat3.text }}&nbsp;{{item.stat3.value}}<span v-if="item.stat3.percent">%</span></p>
                        </td>
                    </tr>
                </tbody>
            </table>
        </div>
        <div class="d-flex justify-content-center" style="height:50vh;" v-else>
            <div class="spinner-border my-auto" role="status">
                <span class="visually-hidden">Loading...</span>
            </div>
        </div>
    </div>
</template>

<script>
/* eslint-disable */
import { ref } from 'vue'
import { useMobile } from "@/store/Mobile"
import { useWeapon } from '@/store/Weapon'
import { useLanguage } from '@/store/Language'
import { storeToRefs } from 'pinia'

export default {
    name: 'WeaponData',
    setup() {
        const { language, text } = storeToRefs(useLanguage())
        
        useWeapon().setDefault()

        const showEquipment = ref(false)
        setTimeout(() => {
            useLanguage().changeLanguage()
            useWeapon().changeLanguage(language.value)
            showEquipment.value = true
        }, "1000")

        const { isMobile } = storeToRefs(useMobile())
        const { 
            T5_Weapon,
            level,
            star,
        } = storeToRefs(useWeapon())

        const setLevel = () => {
            useWeapon().setLevel()
        }
        const setStar = () => {
            useWeapon().setStar()
        }
        console.log(T5_Weapon.value)

        return {
            text,
            showEquipment,
            isMobile,
            T5_Weapon,
            level,
            star,
            setLevel,
            setStar,
        }
    }
}
</script>

<style scoped>
table {
    table-layout:fixed;
    text-align: center;
}
img {
    width: 100%;
    opacity: 1;
}
@media screen and (max-width: 768px) {
    .haven-table>thead>tr>th {
        font-size: xx-small;
    }
}
</style>
