<template>
    <div class="container">
        <WeaponData/>
    </div>
</template>

<script>
/* eslint-disable */
import { ref, watch } from 'vue'
import { useWeapon } from '@/store/Weapon'
import { useLanguage } from '@/store/Language'
import WeaponData from '@/components/WeaponData.vue'
import { storeToRefs } from 'pinia'

export default {
    name: 'EquipmentView',
    components: {
        WeaponData,
    },
    setup() {
        const { language } = storeToRefs(useLanguage())
        watch(language, (nV) => {
            useLanguage().changeLanguage()
            useWeapon().changeLanguage(nV)
        })
    }
}
</script>

